import React from "react"
import Button from '@material-ui/core/Button';
import styles from "./projectList.module.scss"
import { Link } from "gatsby"
import TrimmedContainer from "./trimmed-container";
import FramealotLogo from "../assets/framealotLogo.svg";
import ASPCore from "../assets/NET_Core_Logo.svg";
import StencilJS from "../assets/stencil.svg";
import Webcomponents from "../assets/webcomponents.svg";

const ProjectList = () => {


    return (
        <TrimmedContainer>
            <section className={styles.container}>

                <div className={styles.project}>
                    <div className={styles.framealotLogo}>
                        <a href="https://www.framealot.com" target="blank">
                            <FramealotLogo />
                        </a>
                    </div>

                    <div>
                        <div>
                            <div className={styles.bannerContainer}>
                                <span className={styles.bannerFarmeTxt}>FRAME</span>
                                <span className={styles.bannerAlotTxt}>ALOT</span>
                            </div>
                            <span className={styles.bannerSlogan}>Online custom framing made easy</span>
                        </div>

                        <div className={styles.projectAbout}>

                            <p>
                                I love to build things! I discovered Picture Framing in 2004, since then I have framed countless artworks, certificates, medals and just about anything you can imagine.<br />
                                I quickly realized that I could make my life easier by developing an application to help estimate the frame job prices for clients. And Framealot was born. Today it is a full-fledged eCommerce application.
                           </p>

                            <p>
                                I had a very specific idea of what I wanted to accomplish. After my initial planning, I went through various frameworks such as Magento, NopCommerce and Reaction commerce. I took a bold step and decided to build my own eCommerce platform from scratch. ðŸ˜²
                            </p>

                            <p>
                                I took account of what I have learned as a full-stack developer and selected the tools that will help me get to my goal:
                            </p>
                            <div>

                                <div className={styles.stackInfo}>
                                    <figure className={styles.projectLogo}>
                                        <StencilJS />
                                    </figure>
                                    <p>Used Stencil to build standards based Web Components that run in every browser.</p>
                                </div>

                                <div className={styles.stackInfo}>
                                    <figure className={styles.projectLogo}>
                                        <ASPCore />
                                    </figure>
                                    <p>ASP.NET Core, EF Core for the backend services.</p>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>


                <div>

                </div>



            </section>
        </TrimmedContainer>
    );
};

export default ProjectList;